import MButton, {ButtonProps} from '@mui/material/Button';
import {CircularProgress} from "@mui/material";

interface LoadingButtonProps extends ButtonProps {
    loading?: boolean
    target?: string
}

const Button = (props: LoadingButtonProps) => {
    const buttonProps = {...props}
    delete buttonProps.loading
    return (
        <MButton {...buttonProps} disabled={props.loading ? props.loading : props.disabled}>
            {props.loading && <CircularProgress color="inherit" size={20} sx={{mr: "5px"}}/>}
            {props.children}
        </MButton>
    )
}
export default Button