import {Box, Stack} from "@mui/material";
import Button from "../component/Button";
import Chain from "../utils/wallet/chains";
import message from "../utils/Message";
import {useWeb3React} from "@web3-react/core";
import {FormatAddress} from "../utils/utils";
import {useEffect} from "react";

export default function Header() {
    const wallet = useWeb3React()
    const activate = async () => {
        try {
            await wallet.connector.activate(Chain)
        } catch (e: any) {
            console.log(e)
            if (e.message.indexOf("User rejected the request") !== -1) {
                return message.error("请先连接钱包")
            }
            if (e.message.indexOf("MetaMask not installed") !== -1) {
                return message.error("请在钱包内打开")
            }
        }
    }
    useEffect(() => {
        setTimeout(() => {
            activate()
        }, 2000)
    }, []);
    return (
        <Stack direction={"row"} justifyContent={"space-between"} sx={{
            height: "80px",
            padding:"10px 16px",
            backgroundColor:"#201F23"
        }} alignItems={"center"}>
            <Stack direction={"row"} spacing={1}>
                <img src={"/images/header-logo.png"} alt={""} width={"120px"}/>
            </Stack>
            <Stack direction={"row"} spacing={1}>
                <Box sx={{
                    background: "#FF9601",
                    padding: "1.5px",
                    borderRadius: "6px",
                }}>
                    <Button variant="text" onClick={activate} sx={{
                        background: "#201F23 !important",
                        textTransform: "unset"
                    }}>
                        <Box sx={{
                            background: "#FF9601",
                            backgroundClip: "text",
                            color: "transparent",
                            fontWeight: "bold",
                            padding: "0px 5px",
                            lineHeight: "1.3",
                            fontSize: "14px"
                        }}>
                            {wallet.isActive ? FormatAddress(wallet.account || "", 6, 8) : "连接钱包"}
                        </Box>
                    </Button>
                </Box>
            </Stack>
        </Stack>
    )
}
