import {
    closeSnackbar,
    CustomContentProps,
    enqueueSnackbar,
    OptionsObject,
    SnackbarContent,
    SnackbarProvider
} from "notistack";
import {forwardRef} from "react";
import {Alert} from "@mui/material";
import {AlertColor} from "@mui/material/Alert/Alert";

const CustomMessage = forwardRef<HTMLDivElement, CustomContentProps>(
    ({message, variant, id}: CustomContentProps, ref) => {
        if (variant === "default") {
            variant = "info"
        }
        return (
            <SnackbarContent ref={ref}>
                <Alert sx={{width: "100%"}} severity={variant as AlertColor} onClose={() => {
                    closeSnackbar(id)
                }}>{message}</Alert>
            </SnackbarContent>
        )
    }
)
CustomMessage.displayName = "CustomMessage"
export const MessageProvider = () => {
    return <SnackbarProvider
        autoHideDuration={3000}
        preventDuplicate
        maxSnack={1000}
        Components={{
            info: CustomMessage,
            warning: CustomMessage,
            error: CustomMessage,
            success: CustomMessage,
            default: CustomMessage,
        }}
        anchorOrigin={{
            vertical: 'top',
            horizontal: 'center',
        }}
    />
}

const message = (msg: string, option?: OptionsObject) => {
    enqueueSnackbar(msg, option)
}
message.success = (msg: string, option?: OptionsObject) => {
    message(msg, {...option, variant: "success"})
}
message.info = (msg: string, option?: OptionsObject) => {
    message(msg, {...option, variant: "info"})
}
message.error = (msg: string, option?: OptionsObject) => {
    message(msg, {...option, variant: "error"})
}
message.warning = (msg: string, option?: OptionsObject) => {
    message(msg, {...option, variant: "warning"})
}
export default message


