import React from 'react';
import {MessageProvider} from "./utils/Message";
import {Box, Container} from "@mui/material";
import {hooks, metaMask} from "./utils/wallet/metaMask";
import {RecoilRoot} from "recoil";
import {Web3ReactProvider} from "@web3-react/core";
import Layout from "./page/Layout";

function App() {
    return (
        <Box sx={{
            minHeight: "100%",
            height:"100%",
            color:"#fff",
        }}>
            <Container fixed maxWidth="sm" disableGutters sx={{
                height:"100%",
            }}>
                <MessageProvider/>
                <Web3ReactProvider connectors={[[metaMask, hooks]]}>
                    <RecoilRoot>
                        <Layout/>
                    </RecoilRoot>
                </Web3ReactProvider>
            </Container>
        </Box>
    );
}


export default App;
