import {TransactionResponse, ethers} from "ethers";

export function FormatAddress(address: string, start?: number, end?: number) {
    return address.length > 0 ? address.substring(0, start || 4) + "***" + address.substring(address.length - (end || 4)) : ""
}

export function FormatHash(hash: string) {
    return hash.length > 0 ? hash.substring(0, 5) + "..." + hash.substring(60) : ""
}

export function FormatDateTime(dateTime: string) {
    const newDateTime = new Date(dateTime)
    const padTo2Digits = (num: number) => {
        return num.toString().padStart(2, '0');
    }
    return (
        [
            newDateTime.getFullYear(),
            padTo2Digits((newDateTime.getMonth() + 1)),
            padTo2Digits(newDateTime.getDate())
        ].join('-') +
        ' ' +
        [
            padTo2Digits(newDateTime.getHours()),
            padTo2Digits(newDateTime.getMinutes()),
            padTo2Digits(newDateTime.getSeconds()),
        ].join(':')
    );
}

export function sleep(t: number) {
    return new Promise((resolve) => setTimeout(resolve, t))
}

export async function TxWait(tx: TransactionResponse) {
    while (true) {
        const receipt = await tx.provider.getTransactionReceipt(tx.hash)
        if (receipt) {
            return receipt
        }
        await sleep(1000)
    }
}

export function methodSign(name: string, types: string[], values: any[]): string {
    const id = `${name}(${types.join(",")})`
    const methodId = ethers.id(id)
    const param = ethers.AbiCoder.defaultAbiCoder().encode(types, values)
    return methodId.substring(0, 10) + param.substring(2)
}

export function NumberToTimeFormat(d: number): string {
    if (d < 0) {
        return "00"
    }
    return d >= 10 ? d.toString() : "0" + d.toString()
}
export function isZeroAddress(addr:string):boolean{
    return (addr==="0x0000000000000000000000000000000000000000"||addr.toLowerCase()==="0x000000000000000000000000000000000000dead")
}
